import { useTranslation } from "react-i18next";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../Ui/Border/BorderBottom";
import CreateButton from "../../Ui/Button/CreateButton";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useState } from "react";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import AssignStaffModal from "../../../Department/Institute/AssignStaff/AssignStaffModal";
import { useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { useAdmissionActivateModule } from "../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
const ActivateInstituteAdmissionManager = ({ onClose }) => {
  const { t } = useTranslation();
  const instituteId = useSelector((state) => state.idChange?.id);
  const navigate = useNavigate();
  const params = useParams();
  const [state, setState] = useState("");
  const [assignHead, setAssignHead] = useState(false);
  const [disabled, setDisabled] = useState(false);

  const [admissionActivateModule] = useAdmissionActivateModule();

  const onSelect = (val) => {
    setState(val);
  };
  const onActivateModule = () => {
    if (instituteId) {
      setDisabled(true);
      admissionActivateModule({
        instituteId: instituteId,
        activateModule: {
          sid: state?._id ?? "",
        },
      }).then((res) => {
        if (res.data.admission) {
          onClose();
          navigate(`/q/${params?.username}/institute/member/admission`, {
            state: {
              fid: res.data.admission,
              urlOffSet: "Institute",
            },
          });
          setDisabled(true);
        }
      });
    }
  };

  return (
    <>
      {assignHead ? (
        <AssignStaffModal
          assignHead={assignHead}
          headSelect={onSelect}
          title={t("select_admission_admin")}
          onClose={() => setAssignHead((pre) => !pre)}
          disabledId={state?._id}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("activete_admission_dept")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <div
                className={style.card_container_image}
                onClick={() => setAssignHead(true)}
                style={{
                  cursor: "pointer",
                }}
              >
                <img
                  alt="not found"
                  src={
                    state?.staffProfilePhoto
                      ? `${imageShowUrl}/${state.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                />
                <div className={style.card_container_inner}>
                  {!state?.staffFirstName ? (
                    <>
                      <h6>{t("assign_admission_admin")}</h6>
                      <p>{t("search_from_joined_list")}</p>
                    </>
                  ) : (
                    <>
                      <h6>{`${state?.staffFirstName ?? ""} 
                    ${state?.staffMiddleName ?? ""}
                    ${state?.staffLastName ?? ""}`}</h6>
                      <p>
                        {t("index_number_1")} {state?.staffROLLNO ?? ""}
                      </p>
                    </>
                  )}
                </div>
              </div>
              <CreateButton label="activate" onAction={onActivateModule} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default ActivateInstituteAdmissionManager;
